<!-- 基本信息页面 -->
<template>
	<view class="content">
		<view class="title">
			基本信息
		</view>
		<u-form :model="form" ref="uForm" label-width="150" label-align="left">
			<u-form-item  label="姓名"  prop="name" required>
				<u-input v-model="form.name" placeholder="请输入"/>
			</u-form-item>
			<u-form-item  label="曾用名"  prop="oldName" >
				<u-input v-model="form.oldName" placeholder="请输入"/>
			</u-form-item>
			<u-form-item  label="姓名全拼"  prop="quanPinName" required>
				<u-input v-model="form.quanPinName" placeholder="请输入"/>
			</u-form-item>
			<u-form-item  label="英文名称"  prop="englishName" >
				<u-input v-model="form.englishName" placeholder="请输入"/>
			</u-form-item>
			<u-form-item  label="国籍"  prop="country" required>
				<u-input v-model="form.country" placeholder="请输入"/>
			</u-form-item>
			<u-form-item  label="民族"  prop="nation" required>
				<u-input v-model="form.nation" placeholder="请输入"/>
			</u-form-item>
			<u-form-item  label="证件类型"  prop="cardType" required >
				<!-- <u-input v-model="form.cardType.selectd" type="select" placeholder="请选择" @click="form.cardType.show = true"/> -->
				<u-select v-model="form.cardType.show" :default-value="[0]" :mode="form.cardType.mode" :list="form.cardType.name" @confirm="confirm" ></u-select>
				<text style="width: 100rpx;" @click="form.cardType.show=true">{{form.cardType.selectd}}</text>
				<image src="../../static/下一步.png" style="width: 24rpx; height: 24rpx; margin-left: 400rpx;"  @click="form.cardType.show=true"></image>
			</u-form-item>
		</u-form>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					name: '',
					oldName: '',
					quanPinName: '',
					englishName: '',
					country: '',
					nation: '',
				  cardType:{
						selectd: '请选择',
						mode: 'single-column',
						name: [{
							value: '1',
							label: '身份证'
						},
						{
							value: '2',
							label: '居住证'
						}],
						show: false,
					} 
				},
				rules: {
					name: [
						{
							required: true,
							message: '请输入姓名',
							// 可以单个或者同时写两个触发验证方式
							trigger: 'blur,change'
						},
					  // 2到10个字符之间的判断
						{
							min: 2,
							max: 10,
							message: '姓名长度在2-10个字符之间'
						},
					],
					quanPinName:{
						required: true,
						message: '请输入全拼姓名',
						// 可以单个或者同时写两个触发验证方式
						trigger: 'blur,change'
					},
					country:{
						required: true,
						message: '请输入国籍',
						// 可以单个或者同时写两个触发验证方式
						trigger: 'blur,change'
					},
					nation:{
						required: true,
						message: '请输入民族',
						// 可以单个或者同时写两个触发验证方式
						trigger: 'blur,change'
					}
				}
			}	
		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		methods: {
			nextStep(){
				this.$emit('nextStep')
			},
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						console.log('验证通过');
					} else {
						console.log('验证失败');
					}
				});
			},
			confirm(res) {
				console.log(res)
				this.form.cardType.selectd = res[0].label
			},
		},
		
		onBackPress(){
			return true
		}
	}
</script>

<style scoped>
	.content{
	 display: flex;
	 flex-direction:column;
	}
	.title{
		font-size: 17px;
		padding: 15px 0;
		font-weight: 560;
		border-bottom: 1px solid #e4e7ed;
	}
</style>
